import { Component } from '@angular/core';
import { ServiciosService } from 'src/app/services/servicios.service';
import * as Chart from 'chart.js';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styles: []
})
export class HomeComponent {

  nuevasCanciones: any[] = [];
  loading: boolean;
  error: boolean;
  errorMessaje: string;
  array: any[] = [];
  grupos: any[];
  grupo = {
    'grupo': ''
  };
  productos: any[];
  producto = {
    'grupo': '',
    'producto': ''
  };
  ciudades: any[];
  ciudad = {
    'grupo': '',
    'producto': '',
    'ciudad': ''
  };
  fuentes: any[];
  fuente = '';
  datos: any[];
  grafico = false;
  chart: any = [];
  labels = [];

  constructor(
    private service: ServiciosService
  ) {

    this.loading = true;
    this.error = false;

    this.service.getGrupos()
    .subscribe((data: any) => {
      this.grupos = data;
      // console.log(data);
      this.loading = false;
    }, (errorServicio) => {
      this.errorMessaje = errorServicio.error.error.message;
      console.log(this.errorMessaje);
      this.loading = false;
      this.error = true;
    });
  }

  obtenerProductos(grupo) {
    this.grupo.grupo = grupo;
    // console.log(this.grupo);
    this.service.getProductos(this.grupo)
    .subscribe((data: any) => {
      this.productos = data;
      // console.log(this.productos);
    }, (errorServicio) => {
      this.errorMessaje = errorServicio.error.error.message;
      console.log(this.errorMessaje);
      this.loading = false;
      this.error = true;
  });
  }

  obtenerCiudades(producto) {
    this.producto.grupo = this.grupo.grupo;
    this.producto.producto = producto;
    // console.log(this.producto);
    this.service.getCiudades(this.producto)
    .subscribe((data: any) => {
      this.ciudades = data;
      // console.log(this.ciudades);
    }, (errorServicio) => {
      this.errorMessaje = errorServicio.error.error.message;
      console.log(this.errorMessaje);
      this.loading = false;
      this.error = true;
  });
  }

  obtenerFuentes(ciudad) {
    this.ciudad.grupo = this.grupo.grupo;
    this.ciudad.producto = this.producto.producto;
    this.ciudad.ciudad = ciudad;
    // console.log(this.ciudad);
    this.service.getFuentes(this.ciudad)
    .subscribe((data: any) => {
      this.fuentes = data;
      // console.log(this.fuentes);
    }, (errorServicio) => {
      this.errorMessaje = errorServicio.error.error.message;
      console.log(this.errorMessaje);
      this.loading = false;
      this.error = true;
  });
  }

  asignarFuente(fuente) {
    this.fuente = fuente;
    // console.log(this.fuente);
  }

  prediccion() {
    if (this.grupo.grupo !== '') {
      const obj = {
        'grupo': this.grupo.grupo
      };
      this.array.push(obj);
    }
    if (this.producto.producto !== '') {
      const obj = {
        'producto': this.producto.producto
      };
      this.array.push(obj);
    }
    if (this.ciudad.ciudad !== '') {
      const obj = {
        'ciudad': this.ciudad.ciudad
      };
      this.array.push(obj);
    }
    if (this.fuente !== '') {
      const obj = {
        'fuente': this.fuente
      };
      this.array.push(obj);
    }

    // console.log(this.array);

    this.service.getPredicicon(this.array)
    .subscribe((data: any) => {
      this.datos = data;
      console.log(this.datos);

      this.labels = ['Enero', 'Febrero', 'Marzo', 'Abril',
                      'Mayo', 'Junio', 'Julio', 'Agosto',
                      'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];

      this.chart = new Chart('canvas', {
        type: 'line',
        data: {
          labels: this.labels,
          datasets: this.datos
        },
        options: {
          responsive: true,
          tooltips: {
            mode: 'index',
            intersect: false,
          },
          hover: {
            mode: 'nearest',
            intersect: true
          },
          scales: {
            xAxes: [{
              display: true,
              scaleLabel: {
                display: true,
                labelString: 'Mes'
              }
            }],
            yAxes: [{
              display: true,
              scaleLabel: {
                display: true,
                labelString: 'Precio'
              }
            }]
          }
        }
      });



    }, (errorServicio) => {
      this.errorMessaje = errorServicio.error.error.message;
      console.log(this.errorMessaje);
      this.loading = false;
      this.error = true;
  });
  }
}
