import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ServiciosService {

  constructor(
    private http: HttpClient
  ) { }

  getQuery( query: string) {

    const url = `http://181.49.119.196:5000/api/${ query }`;

    return this.http.get(url);
  }

  postQuery( query: string, obj: any) {

    const url = `http://181.49.119.196:5000/api/${ query }`;

    const body = JSON.stringify( obj );
    const headers = new HttpHeaders ({
      'Content-Type': 'application/json'
    });

    return this.http.post( url, body, {headers});
  }

  postQueryPrediccion( query: string, obj: any[]) {

    const url = `http://181.49.119.196:5000/api/${ query }`;

    const body = JSON.stringify( obj );
    const headers = new HttpHeaders ({
      'Content-Type': 'application/json'
    });

    return this.http.post( url, body, {headers});
  }

  getGrupos () {
    return this.getQuery('grupos/')
                .pipe( map(data => data));
  }

  getProductos(grupo: any) {
    return this.postQuery('productos/', grupo)
                .pipe( map(data => data));
  }

  getCiudades(producto: any) {
    return this.postQuery('ciudades/', producto)
                .pipe( map(data => data));
  }

  getFuentes(ciudad: any) {
    return this.postQuery('fuentes/', ciudad)
                .pipe( map(data => data));
  }

  getPredicicon(array: any[]) {
    return this.postQueryPrediccion('prediccion/', array)
                .pipe( map(data => data));
  }
}
